/*Initializations $init*/

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin: 0;
}

* {
    outline: 0;
}

html,
body {
    overflow-x: hidden;
}


/*Typography $typography*/

h3 {
    font-family: "Raleway", sans-serif;
    font-weight: 500;
    font-size: 32px;
    color: #202020;
    margin-top: 122px;
}

h6 {
    font-family: "Raleway", sans-serif;
    font-size: 16px;
    font-weight: 600;
    color: #202020;
}

h3::after {
    display: block;
    content: "";
    height: 3px;
    width: 46px;
    background-color: #e6e6e6;
    margin: 18px auto;
}

.abs {
    font-family: "Work Sans", sans-serif;
    font-size: 12px;
    color: #535353;
