}


/*Helper Classes $helper*/

.make-center {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.flexer {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -ms-flex-align: center;
    -webkit-align-items: center;
    -webkit-box-align: center;
    align-items: center;
}

.resp-img {
    width: 100%;
    height: auto;
}

.pos-r {
    position: relative;
}


/*Header $header*/

.container {
    width: 1200px;
}

.homepage header:before {
    padding-bottom: 48.385%;
    content: "";
    float: left;
}

.homepage header:after {
    content: "";
    display: table;
    clear: both;
}

.rslides-header {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}

.rslides_nav {
    position: absolute;
    top: 50%;
    color: #fff;
    opacity: 0.3;
}

.rslides_nav {
    padding: 10px;
}

.rslides_nav.prev {
    left: 10px;
}

.rslides_nav.prev:hover {
    left: 13px;
}

.rslides_nav.next {
    right: 10px;
}

.rslides_nav.next:hover {
    right: 13px;
}

.rslides_nav:hover {
    color: #fff;
    opacity: 0.8;
    transition: all 0.3s ease;
}

.rslides_nav:focus {
    color: #fff;
}

