@media (max-width: 767px) {
    .navbar-collapse {
        position: absolute;
        top: 98px;
        right: 0;
        left: 0;
        background-color: rgba(255, 255, 255, 0.9);
        z-index: 299;
        overflow: visible;
        box-shadow: 5px 0 10px 2px rgba(0, 0, 0, 1);
    }
    .navbar-collapse li {
        display: block;
        text-align: center;
    }
    .navbar-collapse>ul>li>a {
        color: #202020;
        letter-spacing: 3px;
    }
    .navbar-collapse>ul>li>.submenu {
        display: none;
    }
    .navbar-collapse>ul>li {
        position: relative;
    }
    .submenu.submenu-list {
        padding: 35px 10px 20px 10px;
        right: 0;
        top: 0;
    }
    .submenu li {
        float: none;
    }
    .submenu.clearfix {
        width: auto;
    }
    .submenu li:not(:last-child) .sub-column {
        padding: 0;
        margin: 0;
        border: none;
    }
}

@media (max-width: 576px) {
    header {
        position: relative;
    }
    .rslides-header {
        height: 100%;
    }
    .rslides-header li,
    .rslides-header li img {
        height: 100%;
        width: auto;
    }
    .rslides_nav {
        top: 60%;
    }
    .bluebutton {
        margin-bottom: 20px;
    }
}

@media (max-width: 380px) {
    header .bluebutton {
        padding: 0;
        width: 100%;
    }
}


